import React, { useState } from 'react';
import {Check} from 'react-feather';
import './notesModal.css';

export default function NotesModal(props) {

	const [note, editNote] = useState(props.note);
	const isCurrent = checkCurrent();

	function saveNote() {
		props.onSaveNote(note, props.index);
		props.onToggleModal(false);
	}

	function checkCurrent() {
		switch (props.index) {
			case props.today:
				return 'today';
			case props.today - 1:
				return 'yesterday';
			case props.today + 1:
				return 'tomorrow';
			default:
				return props.isThisWeek ? 'this week' : 'next week';
		}
	}

	return (
		<>
			<h2>Notes</h2>

			<div className='day-name'>
				<span
					className='this-day'
				>
					{props.thisDay}
				</span>
				{isCurrent &&
					<span
						className='is-current'
					>
						{isCurrent}
					</span>
				}
			</div>

			<div className='text'>
				<textarea
					maxLength='500'
					value={note}
					onInput={event => editNote(event.target.value)}
				/>
			</div>

			<div className='actions-panel'>
				<button
					className='save'
					onClick={saveNote}
				>
					<Check size={16} />
				</button>
			</div>
		</>
	)
}
